:root{--Grid-gutterSize: 25px}*{box-sizing:border-box}html{scroll-behavior:smooth}@media(prefers-reduced-motion: reduce){html{scroll-behavior:auto}}body{margin:0;padding:0;font-family:Arial,sans-serif;background-color:#f0f0f0}h1{font-size:54px}@media(max-width: 767px){h1{font-size:36px}}h2{font-size:36px}@media(max-width: 767px){h2{font-size:28px}}.container{max-width:1200px;margin:0 auto;padding:20px}.app{background-image:url("../../public/images/logos/die-without-stem/blue-die-bg-4.png");background-repeat:repeat;background-size:auto;background-position:0 0}.button{background-color:#007bff;color:#fff;border:none;padding:10px 20px;border-radius:5px;cursor:pointer}.button:hover{background-color:#0056b3}