.contact-section{margin:30px 0}.contact-section .contact-methods-wrapper{display:flex;flex-direction:row;gap:50px;position:relative;width:100%;justify-content:center}@media(max-width: 767px){.contact-section .contact-methods-wrapper{flex-direction:column;gap:20px}}.contact-section .contact-method{position:relative;width:50%;background-color:#f5f5f5;border-radius:25px;padding:30px}@media(max-width: 767px){.contact-section .contact-method{width:100%}.contact-section .contact-method p{line-height:1.5rem}}