.hosts-section{margin:30px 0;position:relative;width:100%}.hosts-section .host--wrapper{display:flex;flex-direction:column;flex-wrap:wrap;justify-content:flex-start;gap:15px}.hosts-section .host{margin-bottom:20px;display:flex;flex-direction:row;width:100%;border-radius:25px;height:300px;overflow:hidden}@media(max-width: 1024px){.hosts-section .host{width:100%;flex-direction:column;margin-bottom:0px;height:auto;border-radius:0px}}.hosts-section .host .host--info{display:flex;flex-direction:column;justify-content:flex-start;background-color:#f5f5f5;height:300px;padding:20px}@media(max-width: 1024px){.hosts-section .host .host--info{margin-left:0px;align-items:flex-start;background-color:rgba(0,0,0,0);height:auto;padding:20px 0px}.hosts-section .host .host--info h4{margin-top:10px;margin-bottom:0px}}.hosts-section .host img{width:fit-content;margin-bottom:10px;position:relative}@media(max-width: 1024px){.hosts-section .host img{margin-bottom:0px}}@media(max-width: 767px){.hosts-section .host img{width:100%;height:auto}}.hosts-section .host p{line-height:2.25rem;font-size:1.1rem;margin:0px}@media(max-width: 1024px){.hosts-section .host p{line-height:1.5rem}}.hosts-section .host p.small{line-height:1.7rem}