.navigation{height:50px;display:flex;flex-direction:row;justify-content:flex-start;gap:30px;align-items:center}@media(max-width: 1024px){.navigation{display:none}}.navigation a{text-decoration:none;font-size:1.25rem;color:#007bff}.mobile-navigation{display:none}@media(max-width: 1024px){.mobile-navigation{display:flex;flex-direction:row;justify-content:flex-start;gap:20px;align-items:center}}.mobile-navigation .menu-button{font-size:40px;position:relative;height:50px;width:50px;top:5px}.mobile-navigation .close-icon{height:25px}.mobile-navigation .hamburger-dash{border-radius:3px;height:3px;margin:5px 0px;border:2px solid #000}.mobile-navigation .mobile-menu{position:absolute;top:90px;left:0px;height:calc(100vh - 90px);width:100%;background-color:#fff;z-index:1;display:flex;flex-direction:column;justify-content:flex-start}.mobile-navigation .mobile-menu .mobile-link{position:relative;width:100%;height:50px;line-height:50px;padding-left:20px;text-decoration:none}